import React, { Component } from 'react';
import { Input, Modal, Form } from 'antd';

const styles = {
    button: {
        backgroundColor: 'lightblue',
    }
}

export default class ChangePW extends Component {

    render() {
        return (
            <div>
                <Modal style={{ display: 'flex', justifyContent: 'stretch' }}
                    visible={true}
                    // onCancel={onCancel} onOk={onCreate}
                    title="修改密码" okText="确认" cancelText='取消'>
                    <Form>
                        <Form.Item label="原密码：">
                        {getFieldDecorator('oldPW')(<Input type="textarea" />)}
                        </Form.Item>
                        <Form.Item label="新密码：">
                        {getFieldDecorator('newPW')(<Input type="textarea" />)}
                        </Form.Item>
                        <Form.Item label="确认密码：">
                        {getFieldDecorator('intro')(<Input type="textarea" />)}
                        </Form.Item>
                    </Form>
                </Modal>
            </div>
        );
    }
}
